import { Component, ElementRef, HostListener, Input, OnInit, ViewChild } from '@angular/core';
import { VhAlgorithm, VhQuerySales } from 'ionic-vhframeworks';
import { Output, EventEmitter } from '@angular/core';
import { Router } from '@angular/router';
import { VhComponent } from 'src/app/sales/components/vh-component/vh-component';
import { LanguageService } from 'src/app/sales/services/language.service';
import { VhType } from 'src/app/sales/interface/vh-type';
@Component({
  selector: 'app-search-goods-inventory',
  templateUrl: './search-goods-inventory.component.html',
  styleUrls: ['./search-goods-inventory.component.scss']
})
export class SearchGoodsInventoryComponent implements OnInit {

  @ViewChild("inputSearchProductInventory") myInputField: ElementRef;
  @Input("visible_products") visible_products: boolean = false;
  @Output("addNewProduct") addNewProduct = new EventEmitter<any>();
  constructor(
    public vhAlgorithm: VhAlgorithm,
    private vhQuerySales: VhQuerySales,
    private router:Router,
    private vhComponent:VhComponent,
    private languageService:LanguageService
  ) { }
  public goodList: any[] = [];
  public searchList: any[] = [];
  public typeGoods = 1
  search: string = '';
  ngOnInit(): void {
    this.changeTypeGood(this.typeGoods);
  }
  ngAfterViewInit() {
    this.myInputField.nativeElement.focus();
  }
  acticeFocus() {
    this.myInputField.nativeElement.focus();
  }
  goBack() {
    this.addNewProduct.emit(false);
  }
  searchProduct(value: string) {
    let tempVal: string = this.vhAlgorithm.changeAlias(value.toLowerCase());
    if (value.length)
      this.searchList = this.vhAlgorithm.searchList(tempVal, this.goodList, ['name', 'barcode'])
    else this.searchList = this.goodList
  }
  chooseProduct(item) {
    this.addNewProduct.emit(item)
  }
  changeTypeGood(event) {
    this.goodList = this.vhQuerySales.getlocalDetailProducts(VhType.PRODUCT).filter(e => !e.delete_hidden).map(e=> {return {...e,...this.vhQuerySales.getUnit_byRatio(e.units, 1), }});
    this.searchList = this.goodList;
    this.typeGoods = event
    this.searchProduct('');
  }
  ngAfterViewChecked() {
    if (document.querySelector(".ant-drawer-body") && document.querySelector(".search-product-input") && document.getElementById("search-product-item")) {
      document.getElementById("search-product-item").style.height = document.querySelector(".ant-drawer-body").clientHeight
        - document.querySelector(".search-product-input").clientHeight + "px";
    }
  }

  scanBarcode(barcode){
    if (barcode.length > 5) {
      let product = this.goodList.filter(item => item.barcode == barcode || item.units?.find(ele => ele.barcode == barcode));
      if (product[0]) {
        let index = product[0].units?.findIndex(ele => ele.barcode.toString() == barcode)
        if (index && index != -1) {
          product[0].ratio = product[0].units[index].ratio;
          product[0].unit_name = product[0].units[index].unit;
        }
        else {
          product[0].ratio = 1;
          product[0].unit_name = product[0].unit;
        }
        this.chooseProduct(product[0] );

      }
      else {
        this.vhComponent.alertMessageDesktop('error', this.languageService.translate("Not found"))

      }
    }
  }

  barcode: string = ''
  barcode_array = [];
  @HostListener("window: keypress", ["$event"])
  /**
   * S kin nhn barcode
   */
  getKeyUp(_event: KeyboardEvent) {
    if (this.router.url === "/sales/dashboard/manage/stock/inventory/detail/cart" ) {
      if (_event.key == "Enter") {
        this.barcode_array.forEach((i, index) => {
          if (i == "Shift") {
            this.barcode_array[index + 1] = this.barcode_array[
              index + 1
            ].toUpperCase();
            this.barcode_array.splice(index, 1);
          }
        });
        this.barcode = this.barcode_array.join("");
        setTimeout(() => {
          this.barcode_array = [];
          this.scanBarcode(this.barcode)
        }, 100);

      } else {
        this.barcode_array.push(_event.key);
      }
      // }
    }
  }

}
