import { ChangeDetectorRef, Component, OnInit } from '@angular/core';
import { ActivatedRouteSnapshot, Router, RouterStateSnapshot, UrlTree } from '@angular/router';
import { VhAlgorithm, VhAuth, VhQuerySales } from 'ionic-vhframeworks';
import { NzModalService } from 'ng-zorro-antd/modal';
import { Observable } from 'rxjs';
import { InventoryA45Component } from 'src/app/sales/components/print/inventory/A45/inventory-a45/inventory-a45.component';
import { VhComponent } from 'src/app/sales/components/vh-component/vh-component';
import { FunctionService } from 'src/app/sales/services/function.service';
import { LanguageService } from 'src/app/sales/services/language.service';
import { VhBillType14Add, VhBillType14BillDetail } from '../bill-type14-interface';

@Component({
  selector: 'bill-type14-add',
  templateUrl: './add.component.html',
  styleUrls: ['./add.component.scss']
})
export class AddComponent implements OnInit {
  visible_products: boolean = false;
  name_product: string = '';
  listProduct: Array<any> = [];
  tempListProduct: Array<any> = [];
  inventory_times: any;
  employees: Array<any> = [];
  employeeSelected: any;

  inventory_detail: VhBillType14BillDetail[] = [];
  search_lists: any = [];
  list_show_products = []
  currentProduct: any = {
    quantity: 0
  };
  inventory: VhBillType14Add
  note: string = "";

  /**--------------------- */
  constructor(
    private router: Router,
    private vhQuerySales: VhQuerySales,
    public vhAlgorithm: VhAlgorithm,
    private vhAuth: VhAuth,
    private vhComponent: VhComponent,
    private lang: LanguageService,
    private cdRef: ChangeDetectorRef,
    private fncService: FunctionService,
    private nzModalService: NzModalService

  ) {
    let inventory_times = this.router.getCurrentNavigation().extras.state.inventory_times;
    if (inventory_times) {
      this.inventory_times = inventory_times;
      this.inventory = {
        bill_type: 14,
        date: new Date(),
        id_branch: this.vhQuerySales.getDefaultBranch()._id,
        id_employee: this.vhAuth.getUser()._id,
        id_inventorytime: this.inventory_times._id,
        note: '',
        bill_code: ''
      }
    }
    // ----lng nghe s kin f4  show nz-drawer----
    window.addEventListener('keydown', (event: KeyboardEvent) => {
      const keycode = event.keyCode;
      if (keycode === 115) {
        this.visible_products = !this.visible_products;
      }
    });
  }

  heightScroll: string;
  ngAfterViewChecked() {
    if (document.querySelector("#product-list") && document.querySelector(".ant-table-thead") && document.querySelector(".product-list-header")) {
      this.heightScroll = document.querySelector("#product-list").clientHeight - document.querySelector(".ant-table-thead").clientHeight
        - document.querySelector(".product-list-header").clientHeight - 30 + "px";
    }
    this.cdRef.detectChanges();
  }



  ngOnInit(): void {
    this.employees = this.vhAuth.getlocalEmployees();
    this.employeeSelected = this.vhAuth.getUser()._id;
    this.inventory.id_employee = (this.employeeSelected);
  }
  goBack() {
    this.router.navigate(["/sales/dashboard/manage/stock/inventory/detail"], { state: { inventory_times: this.inventory_times ? this.inventory_times : this.vhQuerySales.getlocalInventorytime(this.inventory.id_inventorytime) } });
  }

  /**
   * nhn output t component chn sp (app-search-product-import)
   * @param item sp c chn  thm vo bill
   * @example this.closeSearchProductList(product_selected)
   */
  closeSearchProductList(item) {
    // this.visible_products = false;
    if (item) {
      this.currentProduct = item;
      this.currentProduct.quantity = 0;
      if (item.manylot && !item.id_lotproduct) this.openModalChooseLot(item)
      else this.addProductInventory(item)
    }
    else this.visible_products = false;
  }

  /**
   * kim tra s lng  nhp co hp l hay ko
   * @param value sn phm thay i sl
   * @example this.checkedProduct(product_selected)
   */
  checkedProduct(value) {
    if (value.quantity < 0 || value.quantity == null) {
      value.quantity = 0;
    }
    this.setQuantityProduct(value)
  }

  /**
   * bt sk nhn nt xa sp khi bill_detail
   * @param item 
   * @example this.deleteProduct(product_selected)
   */
  deleteProduct(item) {
    this.vhComponent.alertConfirm(
      (this.lang.translate("Confirm")), "",
      (this.lang.translate("Are you sure to delete this product?")),
      "OK",
      (this.lang.translate("Cancel"))
    )
      .then(
        () => {
          this.deleteProductInventory(item);
          this.list_show_products = this.inventory_detail
          this.search_lists = this.inventory_detail
        },
        () => { }
      );
  }
  /**
   * to bill ln DB
   * @param bill_type 
   * @returns  Promise => object = {} or null
   */
  createOrUpdate(bill_type: 13 | 14) {
    return this.vhQuerySales.createBill_Billdetail(this.createOrUpdateInventory(bill_type), this.getCreateUpdateInventoryDetail())
  }
  save_enable = true;
  /**
   * bt sk nhn nt lu phiu hoc lu tm
   * @param bill_type 
   * @param print bin c  check in hay ko
   * @example this.exportInventory(13)
   */
  exportInventory(bill_type: 13 | 14, print?) {
    this.inventory.note = (this.note);
    this.vhComponent.alertConfirm("", "",
      bill_type == 13 ? this.lang.translate('Confirmed Complete ?') : this.lang.translate('Confirmed save ?'),
      "Ok", this.lang.translate('Cancel')
    ).then(() => {
      if (this.save_enable) {
        this.save_enable = false;
        this.vhComponent.showLoadingNotDuration("", "transparent-loading").then(() => {
          this.createOrUpdate(bill_type).then(() => {
            this.save_enable = true;
            this.vhComponent.hideLoading(0).then(() => {
              this.vhComponent.showToast(2000, this.lang.translate("Inventory note has been created successfully"), 'success-toast')
              if (print) {
                this.showPrint()
              } else {
                this.isExit = true
                this.goBack();
              }

            }).catch(()=>{
              
            })
          })
        })
      }
    }).catch(() => { })
  }

  /**
   * lc cc sp trong bill_detail  chn
   * @param value 
   * @example this.searchProduct('')
   */
  searchProduct(value) {
    if (value)
      this.list_show_products = this.vhAlgorithm.searchList(this.vhAlgorithm.changeAlias(value), this.search_lists, ['barcode', 'name'])
    else this.list_show_products = this.search_lists
  }
  /*----------Print------------------*/
  /**
  * ly thng tin d liu trang in
  * @returns Promise<any>
  * @example this.checkPrint()
  */
  checkPrint() {
    return new Promise((resolve, rejects) => {
      let printer = this.vhQuerySales.getLocalPrintPage('page_desktop_inventory')
      if (printer && printer.print_sizes[printer.print_sizes.findIndex(e => e._id == printer.print_size_default)].enable) resolve(printer.print_sizes[printer.print_sizes.findIndex(e => e._id == printer.print_size_default)])
      else rejects("No")
    })
  }

  /** kim tra d liu my in v show component in tng ng
  * @example this.showPrint() 
  */
  showPrint() {
    this.checkPrint().then((printer) => {
      let billInfo = {
        total_checked: this.getTotalRealQuantity(),
        employee: {
          name: this.vhAuth.getlocalEmployee(this.inventory.id_employee).name
        },
        ...this.inventory
      }
      this.vhComponent.showModal(this.renderPrintPage(printer['_id']), { billInfo: billInfo, billDetails: this.inventory_detail, printer: printer }, false, false, `modal-print-${printer['_id']}`).then(modal => {
        modal.onWillDismiss().then(() => { this.isExit = true; this.goBack() })
      })
    }, (err) => {
      this.nzModalService.warning({
        nzTitle: this.lang.translate("Please turn on the printer function"),
        nzOnOk: () => {
          this.isExit = true;
          this.goBack();
        }
      })

    })
  }

  /**
   * tr v component ca trang in tng ng
   * @example this.renderPrintPage('print_size_a4')
   * @returns component
   */
  private renderPrintPage(type: 'K57' | 'K80' | 'K80-3' | 'A4') {
    switch (type) {
      default: return InventoryA45Component
    }
  }
  /** ===============Thot trang==============*/
  isExit: boolean = false;
  checkDeactivate(currentRoute: ActivatedRouteSnapshot, currentState: RouterStateSnapshot, nextState?: RouterStateSnapshot): Observable<boolean | UrlTree> | Promise<boolean | UrlTree> | boolean | UrlTree {
    return nextState.url == '/login' ? true : (this.isExit ? this.isExit : this.vhComponent.alertConfirm(this.lang.translate("Confirm"), "", this.lang.translate("Bn mun thot v khng chnh sa phiu kim ny?"), "OK", (this.lang.translate("Cancel"))).then(() => { return true }).catch(() => { return false }))
  }


  show_modal_select_lots = false;
  list_search_lot = [];
  tempDetail: any;
  /**
   * tr v tn l theo id_lot c chn
   * @param lots 
   * @param _id 
   * @returns string
   * @example this.getLotNumber([], 'DJWOIDHIEUGHWDSJDNWIHD')
   */
  getLotNumber(lots, _id) {
    let lot = lots.find(item => item._id == _id);
    return lot.lot_number + '\n' + this.fncService.formatDate(lot.date_mfg) + ' - ' + this.fncService.formatDate(lot.date_exp)
  }
  /**
   * lc danh sch l ca sp theo s l (lot_number)
   * @param value 
   */
  searchLot(value) {
    this.tempDetail.lots = this.vhAlgorithm.searchList(this.vhAlgorithm.changeAlias(value), this.list_search_lot, ['lot_number'])
  }
  /**
   * m modal chn l cho sp
   * @param data 
   * @example this.openModalChooseLot(bill_detail_selected)
   */
  openModalChooseLot(data) {

    this.show_modal_select_lots = true;
    data.lots = data.lots.map(e => { return { ...e, quantity: 0 } })
    this.tempDetail = data
    this.list_search_lot = this.tempDetail.lots
    

  }


  /**
   * bt sk nhn nt ok ca modal chn l sp
   * @example this.handleChooseLot()
   */
  handleChooseLot() {
    

    this.show_modal_select_lots = false;
    this.tempDetail.lots = this.list_search_lot;
    for (let i of this.tempDetail.lots) {
      if (i.choose) {
        this.addProductInventory({ ...this.tempDetail, id_lotproduct: i._id, lot_number: i.lot_number });
      }
    }
  }

  /**
   * bt sk nhn nt hy modal chn l sp
   * @example this.handleCancelChooseLot()
   */
  handleCancelChooseLot() {
    this.show_modal_select_lots = false;
    this.tempDetail.lots = this.list_search_lot
  }

  // ********************* X l Inventory detail ***************************
  public addProductInventory(value: any) {
    if (value['id_subproduct']) {
      if (value.lot_number) {
        let index = this.inventory_detail.findIndex(detail => detail['lot_number'] == value['lot_number'] && detail['id_lotproduct'] == value['id_lotproduct'])
        if (index != -1) {
          if (this.inventory_detail[index]['quantity'] < 9999999999) this.inventory_detail[index]['quantity'] += 1
        } else {
          this.addBill_Detail(value)
        }
      }
      else {
        let index = this.inventory_detail.findIndex(detail => detail['id_subproduct'] == value['id_subproduct'] && detail['ratio'] == value['ratio'])
        if (index != -1) {
          if (this.inventory_detail[index]['quantity'] < 9999999999) this.inventory_detail[index]['quantity'] += 1
        } else {
          this.addBill_Detail(value)
        }
      }
    }
    else {
      if (value.lot_number) {
        let index = this.inventory_detail.findIndex(detail => detail['lot_number'] == value['lot_number'] && detail['id_lotproduct'] == value['id_lotproduct'])
        if (index != -1) {
          if (this.inventory_detail[index]['quantity'] < 9999999999) this.inventory_detail[index]['quantity'] += 1
        } else {
          this.addBill_Detail(value)
        }
      }
      else {
        let index = this.inventory_detail.findIndex(detail => detail['id_product'] == value['_id'] && detail['ratio'] == value['ratio'])
        if (index != -1) {
          if (this.inventory_detail[index]['quantity'] < 9999999999) this.inventory_detail[index]['quantity'] += 1
        } else {
          this.addBill_Detail(value)
        }
      }
    }
  }
  /**
     * x l cc trng ca sp c chn v thm vo mng local
     * @param value 
     * @example  this.addBill_Detail(product_selected)
     */
  addBill_Detail(value: any) {
    let detail: VhBillType14BillDetail = {
      name: value.name,
      id_product: value._id,
      quantity: value.quantity ? value.quantity : 0,
      ptype: value.type,
      unit: value.unit,
      units: value.units ? value.units : [],
      ratio: value.ratio ? value.ratio : 1,
    }

    if (value.lot_number) detail['lot_number'] = value.lot_number;
    if (value.id_lotproduct) detail['id_lotproduct'] = value.id_lotproduct;
    if (value.lots) {
      detail['lots'] = value.lots;
      detail['quantity'] = value.lots.find(e => e._id == value.id_lotproduct).quantity
    }
    if (value.id_subproduct) detail['id_subproduct'] = value.id_subproduct;

    this.inventory_detail.unshift(detail)
    this.list_show_products = this.inventory_detail;
    this.search_lists = this.inventory_detail
  }
  /**
   * set quantity vo bill_detail c chn
   * @param product 
   * @example this.setQuantityProduct(product_selected)
   */
  public setQuantityProduct(product) {
    let index: number = this.inventory_detail.findIndex(item => item['id_product'] == product._id);
    if (index != -1) {
      this.inventory_detail[index]['quantity'] = product.quantity;
    }
  }
  /**
   * xa bill_detail trong mng local
   * @param index 
   * @example this.deleteProductInventory(1) 
    */
  public deleteProductInventory(index) {
    this.inventory_detail.splice(index, 1);

  }
  /**
   * tr v bill  lu DB
   * @param bill_type 
   * @returns objetc
   * @example let bill = this.createOrUpdateInventory(13)
   */
  public createOrUpdateInventory(bill_type: 13 | 14) {
    if (bill_type) this.inventory.bill_type = (bill_type);
    return this.inventory
  }
  /**
   * tr v mng bill_detail  c clean  lu DB
   * @returns array[object]
   * @example let bill_details = this.getCreateUpdateInventoryDetail()
   */
  public getCreateUpdateInventoryDetail(): VhBillType14BillDetail[] {
    return this.inventory_detail.map(item => {
      let detail: VhBillType14BillDetail = {
        id_product: item.id_product, quantity: item.quantity ?item.quantity : 0 , ptype: item.ptype,
        ratio: item.ratio
      }
      if (item.id_lotproduct) detail['id_lotproduct'] = item.id_lotproduct
      if (item.id_subproduct) detail['id_subproduct'] = item.id_subproduct
      return detail
    })
  }
  /**
   * tr v tng s lng sp  kim trong bill
   * @returns number
   * @example this.getTotalRealQuantity()
   */
  getTotalRealQuantity(): number {
    let total = 0;
    for (let i of this.inventory_detail) {
      total += i.quantity ? i.quantity : 0
    }
    return total;
  }
}
